"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";

interface Result {
    id: number;
    student_id: number;
    subject_id: number;
    marks_obtained: number;
    total_marks: number;
    exam_type: string;
    semester: string;
    academic_year: string;
    date_declared: string;
}

interface Student {
    id: number;
    full_name: string;
    roll_number: string;
    department: string;
    year: string;
}

export default function AdminResultsPage() {
    const router = useRouter();
    const [results, setResults] = useState<Result[]>([]);
    const [students, setStudents] = useState<Student[]>([]);
    const [loading, setLoading] = useState(true);
    const [showForm, setShowForm] = useState(false);
    const [formData, setFormData] = useState({
        student_id: 0,
        subject_id: 1,
        marks_obtained: 0,
        total_marks: 100,
        exam_type: "Internal",
        semester: "Sem 1",
        academic_year: "2025-26",
    });

    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async () => {
        try {
            const token = localStorage.getItem("token");

            if (!token) {
                router.push("/auth");
                return;
            }

            // Get students
            const studentsRes = await fetch("http://localhost:8000/students/", {
                headers: { Authorization: `Bearer ${token}` },
            });

            if (studentsRes.ok) {
                const studentsData = await studentsRes.json();
                setStudents(studentsData);
                if (studentsData.length > 0) {
                    setFormData((prev) => ({ ...prev, student_id: studentsData[0].id }));
                }
            }

            // For demo, get results for first student if available
            if (students.length > 0) {
                const resultsRes = await fetch(`http://localhost:8000/results/student/${students[0].id}`, {
                    headers: { Authorization: `Bearer ${token}` },
                });

                if (resultsRes.ok) {
                    const resultsData = await resultsRes.json();
                    setResults(resultsData);
                }
            }
        } catch (error) {
            console.error("Error fetching data:", error);
        } finally {
            setLoading(false);
        }
    };

    const handleCreateResult = async (e: React.FormEvent) => {
        e.preventDefault();

        try {
            const token = localStorage.getItem("token");
            const res = await fetch("http://localhost:8000/results/", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${token}`,
                },
                body: JSON.stringify(formData),
            });

            if (res.ok) {
                setShowForm(false);
                fetchData();
            }
        } catch (error) {
            console.error("Error creating result:", error);
        }
    };

    const calculatePercentage = (obtained: number, total: number) => {
        return ((obtained / total) * 100).toFixed(2);
    };

    const getGrade = (percentage: number) => {
        if (percentage >= 90) return "A+";
        if (percentage >= 80) return "A";
        if (percentage >= 70) return "B+";
        if (percentage >= 60) return "B";
        if (percentage >= 50) return "C";
        if (percentage >= 40) return "D";
        return "F";
    };

    if (loading) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-violet-50 to-purple-100 flex items-center justify-center">
                <div className="text-xl text-gray-700">Loading results...</div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gradient-to-br from-violet-50 to-purple-100 p-8">
            <div className="max-w-7xl mx-auto">
                <div className="mb-8 flex justify-between items-center">
                    <div>
                        <h1 className="text-4xl font-bold text-gray-800 mb-2">Results Management</h1>
                        <p className="text-gray-600">Publish and manage student exam results</p>
                    </div>
                    <button
                        onClick={() => setShowForm(!showForm)}
                        className="px-6 py-3 bg-purple-600 text-white rounded-lg hover:bg-purple-700 transition-colors font-semibold shadow-lg"
                    >
                        {showForm ? "Cancel" : "+ Add Result"}
                    </button>
                </div>

                {showForm && (
                    <div className="bg-white rounded-xl shadow-lg p-6 mb-8">
                        <h2 className="text-2xl font-semibold text-gray-800 mb-4">Add New Result</h2>
                        <form onSubmit={handleCreateResult} className="space-y-4">
                            <div className="grid grid-cols-2 gap-4">
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Student</label>
                                    <select
                                        required
                                        className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                                        value={formData.student_id}
                                        onChange={(e) => setFormData({ ...formData, student_id: parseInt(e.target.value) })}
                                    >
                                        {students.map((student) => (
                                            <option key={student.id} value={student.id}>
                                                {student.full_name} ({student.roll_number})
                                            </option>
                                        ))}
                                    </select>
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Subject ID</label>
                                    <input
                                        type="number"
                                        required
                                        className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                                        value={formData.subject_id}
                                        onChange={(e) => setFormData({ ...formData, subject_id: parseInt(e.target.value) })}
                                    />
                                </div>
                            </div>
                            <div className="grid grid-cols-2 gap-4">
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Marks Obtained</label>
                                    <input
                                        type="number"
                                        required
                                        min="0"
                                        className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                                        value={formData.marks_obtained}
                                        onChange={(e) => setFormData({ ...formData, marks_obtained: parseInt(e.target.value) })}
                                    />
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Total Marks</label>
                                    <input
                                        type="number"
                                        required
                                        min="1"
                                        className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                                        value={formData.total_marks}
                                        onChange={(e) => setFormData({ ...formData, total_marks: parseInt(e.target.value) })}
                                    />
                                </div>
                            </div>
                            <div className="grid grid-cols-3 gap-4">
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Exam Type</label>
                                    <select
                                        required
                                        className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                                        value={formData.exam_type}
                                        onChange={(e) => setFormData({ ...formData, exam_type: e.target.value })}
                                    >
                                        <option value="Internal">Internal</option>
                                        <option value="Final">Final</option>
                                        <option value="Assignment">Assignment</option>
                                    </select>
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Semester</label>
                                    <select
                                        required
                                        className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                                        value={formData.semester}
                                        onChange={(e) => setFormData({ ...formData, semester: e.target.value })}
                                    >
                                        {["Sem 1", "Sem 2", "Sem 3", "Sem 4", "Sem 5", "Sem 6", "Sem 7", "Sem 8"].map((sem) => (
                                            <option key={sem} value={sem}>{sem}</option>
                                        ))}
                                    </select>
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">Academic Year</label>
                                    <input
                                        type="text"
                                        required
                                        className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
                                        value={formData.academic_year}
                                        onChange={(e) => setFormData({ ...formData, academic_year: e.target.value })}
                                        placeholder="2025-26"
                                    />
                                </div>
                            </div>
                            <button
                                type="submit"
                                className="w-full px-6 py-3 bg-purple-600 text-white rounded-lg hover:bg-purple-700 transition-colors font-semibold"
                            >
                                Add Result
                            </button>
                        </form>
                    </div>
                )}

                <div className="bg-white rounded-xl shadow-lg p-6">
                    <h2 className="text-2xl font-semibold text-gray-800 mb-6">Recent Results</h2>
                    {results.length === 0 ? (
                        <div className="text-center py-12">
                            <div className="text-6xl mb-4">ðŸ“Š</div>
                            <h3 className="text-xl font-semibold text-gray-700 mb-2">No Results</h3>
                            <p className="text-gray-500">Add results to see them here.</p>
                        </div>
                    ) : (
                        <div className="overflow-x-auto">
                            <table className="w-full">
                                <thead>
                                    <tr className="border-b-2 border-gray-200">
                                        <th className="text-left p-3 font-semibold text-gray-700">Student ID</th>
                                        <th className="text-left p-3 font-semibold text-gray-700">Subject</th>
                                        <th className="text-left p-3 font-semibold text-gray-700">Marks</th>
                                        <th className="text-left p-3 font-semibold text-gray-700">Percentage</th>
                                        <th className="text-left p-3 font-semibold text-gray-700">Grade</th>
                                        <th className="text-left p-3 font-semibold text-gray-700">Type</th>
                                        <th className="text-left p-3 font-semibold text-gray-700">Semester</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {results.map((result) => {
                                        const percentage = parseFloat(calculatePercentage(result.marks_obtained, result.total_marks));
                                        const grade = getGrade(percentage);

                                        return (
                                            <tr key={result.id} className="border-b border-gray-100 hover:bg-gray-50">
                                                <td className="p-3">{result.student_id}</td>
                                                <td className="p-3">{result.subject_id}</td>
                                                <td className="p-3 font-semibold">{result.marks_obtained}/{result.total_marks}</td>
                                                <td className="p-3">{percentage}%</td>
                                                <td className="p-3">
                                                    <span className={`px-3 py-1 rounded-full text-sm font-semibold ${percentage >= 60 ? 'bg-green-100 text-green-800' :
                                                            percentage >= 40 ? 'bg-yellow-100 text-yellow-800' :
                                                                'bg-red-100 text-red-800'
                                                        }`}>
                                                        {grade}
                                                    </span>
                                                </td>
                                                <td className="p-3">{result.exam_type}</td>
                                                <td className="p-3">{result.semester}</td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </table>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
